.modal {
  display: none; 
  position: fixed; 
  z-index: 1; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgba(0,0,0,0.3); 

  .modal-content {
    background-color: #fefefe;
    margin: 15% auto; 
    padding: 20px;
    border: none;
    width: 80%; 
    animation-name: zoom;
    animation-duration: 0.6s;

    .close {
      color: #aaa;
      float: right;
      font-size: $medium;
      font-weight: $bold;

      &:hover, &:focus {
        color: black;
        text-decoration: none;
        cursor: pointer;
      }    
    }
  }
}

@keyframes zoom {
  from {
    transform:scale(0)
  }
  to {
    transform:scale(1)
  }
}